
export const AABBintersect2D = (v0: NVector3[], v1: NVector3[]): boolean => {
    const v0minX = v0[0].x;
    const v0maxX = v0[1].x;
    const v1minX = v1[0].x;
    const v1maxX = v1[1].x;
    const v0minY = v0[0].y;
    const v0maxY = v0[1].y;
    const v1minY = v1[0].y;
    const v1maxY = v1[1].y;
    // const v0minZ = 0;
    // const v0maxZ = 0;
    // const v1minZ = 0;
    // const v1maxZ = 0;
    return (v0minX <= v1maxX && v0maxX >= v1minX) &&
        (v0minY <= v1maxY && v0maxY >= v1minY); // &&
    // (v0minZ <= v1maxZ && v0maxZ >= v1minZ);
};
export const distance = (v0: NVector3, v1: NVector3): number => {
    return Math.sqrt((v0.x - v1.x) * (v0.x - v1.x) + (v0.y - v1.y) * (v0.y - v1.y) + (v0.z - v1.z) * (v0.z - v1.z));
    // return NWebassemblyController.ctrller.Distance(v0.x, v0.y, v0.z, v1.x, v1.y, v1.z);
}

export class NVector3 {
    public static getMidVec(v0: NVector3, v1: NVector3): NVector3 {
        return new NVector3(v0.x + ((v1.x - v0.x) * 0.5), v0.y + ((v1.y - v0.y) * 0.5), v0.z + ((v1.z - v0.z) * 0.5));
    }
    public static distanceTo(v0: NVector3, v1: NVector3): number {
        return Math.sqrt((v0.x - v1.x) * (v0.x - v1.x) + (v0.y - v1.y) * (v0.y - v1.y) + (v0.z - v1.z) * (v0.z - v1.z));
    }
    public static Clone(v: NVector3): NVector3 {
        return NVector3.DeepCopy(v);
    }
    public static DeepCopy(v: NVector3): NVector3 {
        const out = new NVector3(v.x, v.y, v.z);
        out.long = v.long; out.lat = v.lat;
        // out.proX = v.proX; out.proY = v.proY; out.proZ = v.proZ;
        return out;
    }
    public static Dot(a: NVector3, b: NVector3) {
        return a.x * b.x + a.y * b.y + a.z * b.z;
    }
    public static ZAxis(): NVector3 {
        return new NVector3(0, 0, 1);
    }
    public static YAxis(): NVector3 {
        return new NVector3(0, 1, 0);
    }
    public static Zero(): NVector3 {
        return new NVector3(0, 0, 0);
    }
    public static Origin(): NVector3 {
        return new NVector3(0, 0, 0);
    }
    public x: number = 0;
    public y: number = 0;
    public z: number = 0;
    public long: number = 0;
    public lat: number = 0;
    public elevation: number = 0;

    public d: boolean = true;
    public isRenderable: boolean = true;

    constructor(x: number = 0, y: number = 0, z: number = 0) {
        this.x = x;
        this.y = y;
        this.z = z;
    }
    public Set(x: number, y: number, z: number): NVector3 {
        this.x = x;
        this.y = y;
        this.z = z;
        return new NVector3(this.x, this.y, this.z);
    }
    public scale(v: number): NVector3 {
        this.x *= v;
        this.y *= v;
        this.z *= v;
        return new NVector3(this.x, this.y, this.z);
    }
    public Mult(v: number): NVector3 {
        this.x *= v;
        this.y *= v;
        this.z *= v;
        return new NVector3(this.x, this.y, this.z);
    }
    public DivByVal(v: number): NVector3 {
        this.x /= v;
        this.y /= v;
        this.z /= v;
        return new NVector3(this.x, this.y, this.z);
    }
    public DivByVec(v: NVector3): NVector3 {
        this.x /= v.x;
        this.y /= v.y;
        this.z /= v.z;
        return new NVector3(this.x, this.y, this.z);
    }
    public Add(v: NVector3): NVector3 {
        this.x += v.x;
        this.y += v.y;
        this.z += v.z;
        return new NVector3(this.x, this.y, this.z);
    }
    public Sub(v: NVector3): NVector3 {
        this.x -= v.x;
        this.y -= v.y;
        this.z -= v.z;
        return new NVector3(this.x, this.y, this.z);
    }
    public Negate() {
        this.x *= -1;
        this.y *= -1;
        this.z *= -1;
        return new NVector3(this.x, this.y, this.z);
    }
    public Equals(v: NVector3) {
        return this.x === v.x && this.y === v.y && this.z === v.z;
    }
    public distanceTo(v1: NVector3): number {
        return NVector3.distanceTo(this, v1);
    }
    public normalize(): NVector3 {
        let len: number = Math.sqrt(this.x * this.x + this.y * this.y + this.z * this.z);
        if (len < 0.0001) {
            return this;
        }
        this.x = this.x / len;
        this.y = this.y / len;
        this.z = this.z / len;
        return this;
    }
    public Clone(): NVector3 {
        const other = new NVector3(this.x, this.y, this.z);
        other.long = this.long; other.lat = this.lat; other.elevation = this.elevation;
        return other;
    }
    public Length(): number {
        const len = Math.sqrt(this.x * this.x + this.y * this.y + this.z * this.z);
        if (len < 0.00001) {
            return 0.00001;
        }
        return len;
    }
    public Unitize(): NVector3 {
        return this.normalize();
    }
    // public DistanceByArray(vs: number[]) {
    //     return NVector3.DistanceByArray([this.x, this.y, this.z], vs);
    //   }
    //   public Distance2(v: NVector3): number {
    //     return NVector3.Distance2(new NVector3(this.x, this.y, this.z), v);
    //   }
    //   public DotProduct(v: NVector3): number {
    //     return NVector3.DotProduct(new NVector3(this.x, this.y, this.z), v);
    //   }
    //   public CrossProduct(v: NVector3): NVector3 {
    //     return NVector3.CrossProduct(new NVector3(this.x, this.y, this.z), v);
    //   }
    //   public SquareLength(): number {
    //     let len: number = NVector3.Length(new NVector3(this.x, this.y, this.z));
    //     return len * len;
    //   }
    //   public FadeExp(attr: NVector3, dVal: number) {
    //     let v = new NVector3(this.x, this.y, this.z);
    //     v.Sub(attr);
    //     return Math.exp(-dVal * v.SquareLength());
    //   }
    public GetAngle(): number {
        return Math.atan2(this.y, this.x);
    }
    public GetAngleDegree() {
        return 180 * this.GetAngle() / Math.PI;
    }
    public ToJSON() {
        /* */
    }
    public ToString(): string {
        let t: string = 'x: ' + this.x.toFixed(3).toString() + ' , y: ' + this.y.toFixed(3).toString() + ', z: ' + this.z.toFixed(3).toString();
        return t;
    }
    public ToFloat32Array() {
        return new Float32Array([this.x, this.y, this.z]);
    }
}


export class CPoint {
    public vec: NVector3;
    public id: string = '';
    public numVisit: number = 0;

    public isCollapse: boolean = false;

    public isHidden: boolean = false;
    public isRenderable: boolean = true;

    public isSelected: boolean = false;
    public isHover: boolean = false;
    public collision: Collision;
    constructor(x: number, y: number) {
        this.vec = new NVector3(x, y, 0);
        this.collision = new Collision(this);
    }
}
export class Collision {
    base: CPoint;
    isCollided: boolean = false;

    neighborShapes: CPoint[] = [];

    minVec: NVector3;
    maxVec: NVector3;

    isVisited: boolean = false;
    numOrder: number = -1;

    constructor(base: CPoint) {
        this.base = base;
        const r = 5;
        this.minVec = new NVector3(this.base.vec.x - r, this.base.vec.y - r, 0);
        this.maxVec = new NVector3(this.base.vec.x + r, this.base.vec.y + r, 0);
    }
    public Reset(){
        this.neighborShapes = [];
        this.numOrder = -1;
        this.base.numVisit = 0;
        this.base.isCollapse = false;
        this.isCollided = false;
        this.isVisited = false;
    }
}